const selectedPlatform = (platform) => {
    switch (platform) {
        case 'p-html':
            return 'HTML'
        case 'p-wordpress':
            return 'Wordpress'
        case 'p-wix':
            return 'Wix'
        case 'p-squarespace':
            return 'Squarespace'
        case 'p-shopify':
            return 'Shopify'
        case 'p-other':
            return 'Other'
        default:
            return `HTML`
    }
}

